<template>
    <tr>
    <th scope="row">{{index+1}}</th>
    <td>{{user['name']}}</td>
    <td>{{user['value']}}</td>
    <td><i class="fa fa-trash text-danger" @click="deleteCategory"></i></td>
    </tr>
</template>

<script>
import axios from 'axios';

export default {
    props : ["user","index"],
    methods : {
        async deleteCategory() {
            const data = {
                "id" : this.user['_id'],
            }
            await axios.post(`${this.$hostname}/deleteCategory`,data);
            this.$emit('refresh');
        }
    }
}
</script>

